.home {
  max-width: 1500px;
  margin: 0 auto;
}
.home__image {
  width: 100%;
  object-fit: contain;
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
  z-index: -1;
  margin-bottom: -200px;
}
.home__row {
  display: flex;
  z-index: 1;
  margin: 0 5px;
}
